@import url('https://fonts.googleapis.com/css?family=Source+Sans+Pro:600,700,900&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  font-family: 'Source Sans Pro', sans-serif;
  color: #56175d;
  background: url(components/images/bg_copenhagen.jpg ) no-repeat center center fixed; 
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
}
header {
  background-color: #fff;
  border-bottom: 1px solid black;
  padding: 1em 2em;
  display: grid;
  grid-template-columns: 200px 1fr 1fr;
  height: 90px;
}
nav {
  justify-self: right;
}
nav ul{
  padding-top: .8em;
}
nav ul li {
  float: left;
  padding: 15px 20px;
  list-style-type: none;
}
#changecity select {
  margin: 10px 0 0 5vw;
  background-color: rgba(86,23,93,0.2);
  font-weight: 700;
  color: #56175d;
  height: 40px;
  width: 35vw;
  font-size: 1em;
  border: 1px solid #56175d;
}
#changecity button {
  background-color: #56175d;
  color: #fff;
  height: 50px;
  width: 40vw;
  font-size: 1em;
}
#changecity button:hover {
  background-color: #340e38;
}
#wrapper {
  background-color: rgba(255,255,255,0.7);
  padding: 4em 2em;
  display: grid;
  grid-template-columns: 3fr 2fr;
  min-height: calc(100vh - 90px);
}
#cityname h1 {
    font-family: 'Source Sans Pro', sans-serif;
    text-transform: uppercase;
    font-size: 6vw;
    font-size: 600;
}
#cityname h1 span{
    font-size: 3vw;
}
#tempcontainer {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  padding: 4vw 1vw;
}
#tempcontainer h4 {
  font-size: 5vw;
  position: relative;
}
#tempcontainer h4 span {
  font-size: 2vw;
  position: absolute;
  top: 15px;
}
#icon {
  text-align: center;
  font-size: 10vw;
}
#textinfo {
  text-align: center;
}
#textinfo h3 {
  text-align: center;
  font-size: 4vw;
}
#textinfo h4 {
  text-align: center;
  font-size: 2vw;

}